
.formulario{
    background-image: var(--fondo-hojas);
    background-repeat: repeat;
    background-size: 10rem;
    
    text-align: center;
    width: 50%;
    height: max-content;
    border-radius: 50px;
    margin: auto;
    margin-top: 87px;
    margin-bottom: 100px;
    padding: 20px;

    h2{
        display: block;
        margin: auto;
        color: var(--color-letra);
    }
    
    input{
        width: 50%;
        height: 40px;
        background-color: var(--color-input);
        border: none;
        display: block;
        margin: auto;
        margin-top: 60px;
        margin-bottom: 40px;
        padding-left: 10px;
        border-radius: 10px;
        
        &:last-of-type{
            margin-bottom: 10px;
        }

        &::placeholder{
            color: var(--color-letra-input);
            font-weight:600;
            font-size: calc(0.5em + 0.5vw);
        }

    }
    
    
    a{
        color: var(--color-letra);
        text-decoration: none;
        font-size: calc(0.6em + 0.3vw);
        font-weight:bolder;


    }
    

    .btn-medium-desktop {
        margin-bottom: 20px;
        margin-top: 30px;
        font-weight:600;
    }

}

.imagenBienvenida{
    position: absolute;
    top: 650px;
    right: 20px;
    img{
        width: 327px;
        height:auto;
    }
}