
.btn-large-desktop{
    width: 30%;
    height: 50px;
    font-size: large;
    font-weight: bold;
}

.btn-medium-desktop{
    width: 170px;
    height: 50px;
    font-size: 1rem;
    font-weight: 600;
    
}

.btn-small{
    width: 122px;
    height: 44px;
    font-size: 1.5rem;
    font-weight: bold;
    
}

button{
    width: 75px;
    height: 40px;
    background-color: var(--color-boton);
    color: var(--color-primario);
    border: var(--color-boton);
    border-radius: 40px;
    cursor: pointer;
}

button:hover{
    background-color: var(--color-hover);
    color: var(--color-primario);
}